.loader {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 3px dashed whitesmoke;
  animation: rotate 1s infinite linear;
  margin: 0 auto;
}

@keyframes rotate {
  from {
    transform: rotate(0deg) scale(1);
  }

  to {
    transform: rotate(360deg) scale(1.5);
  }
}